<template>
  <GreetingPage v-if="pageName==='GreetingPage'" :set-page="setActivePage" :theme="theme" :change-theme="changeTheme"/>
  <d v-else>
    <button class="blackout" id="blackout"
            onclick="(() => {document.getElementById('sidebar').style.width = '0'; document.getElementById('blackout').style.display = 'none';})()"></button>
    <div class="line page_back left_bar" style="border-left: none; border-bottom: none; border-top: none;" id="sidebar">

      <div class="nav-item hs" style="display: grid; grid-auto-flow: column; border-left: none; border-right: none; border-top: none;">
        <h4 class="text company_name" ><b>Zephyr Studios</b></h4>
        <a class="side_cross"
           onclick="(() => {document.getElementById('sidebar').style.width = '0'; document.getElementById('blackout').style.display = 'none';})()">&times;</a>
      </div>
      <NavBar :page-name="pageName" :set-page-name="setActivePage" :open="openNavBar"/>
    </div>
    <div class="navbar-expand body line" style="border-left: none; border-right: none; border-top: none; position: fixed; z-index: 1;">
      <span class="side_open"
            onclick="(() => {document.getElementById('sidebar').style.width = '250px'; document.getElementById('blackout').style.display = 'block';})()">&#9776;</span>
      <div class="nav-item" style="display: grid; grid-auto-flow: column;">
        <img src="@/assets/company_logo.png" class="company_logo"/>
        <h4 class="text company_name" v-on:click="setActivePage('GreetingPage')"><b>Zephyr Studios</b></h4>
      </div>
      <div class="nav-item" style="">
        <ThemeButton :change="changeTheme" :theme="theme" class="social"/>
        <a  :href="refToDiscord" style="text-decoration: none;" target="_blank">
          <img v-if="theme" src="@/assets/discord_logo_dark.png" class="social"/>
          <img v-else src="@/assets/discord_logo.png" class="social"/>
        </a>
        <a :href="refToTwitter" style="text-decoration: none;" target="_blank">
          <img v-if="theme" src="@/assets/twitter_logo_dark.png" class="social"/>
          <img v-else src="@/assets/twitter_logo.png" class="social"/>
        </a>
      </div>
    </div>
    <div style="display: flex; margin-top: min(15vw, 90px); position: absolute;">
      <div class="body side_bar hs" style="border-left: none; border-bottom: none; border-top: none; position: fixed;">
        <NavBar :page-name="pageName" :set-page-name="setActivePage" :open="openNavBar"/>
      </div>
      <d class="body side_bar"/>
      <StakeIntroduction :set-page="setActivePage" v-if="pageName==='StakeIntroduction'"/>
      <StakeGetStarted :set-page="setActivePage" v-if="pageName==='StakeGetStarted'"/>
      <StakeInstance :set-page="setActivePage" v-if="pageName==='StakeInstance'"/>
      <StakeStandard :set-page="setActivePage" v-if="pageName==='StakeStandard'"/>
      <StakeRewards :set-page="setActivePage" v-if="pageName==='StakeRewards'"/>
      <StakeVirtualMachine :set-page="setActivePage" v-if="pageName==='StakeVirtualMachine'"/>
      <StakeLog :set-page="setActivePage" v-if="pageName==='StakeLog'"/>
      <StakeVerifyUpload :set-page="setActivePage" v-if="pageName==='StakeVerifyUpload'"/>
      <StakeDeposits :set-page="setActivePage" v-if="pageName==='StakeDeposits'"/>
      <StakeClusters :set-page="setActivePage" v-if="pageName==='StakeClusters'"/>
      <UISetUp :set-page="setActivePage" v-if="pageName==='UISetUp'"/>
      <UICustom :set-page="setActivePage" v-if="pageName==='UICustom'"/>
      <SecurityPage :set-page="setActivePage" v-if="pageName==='SecurityPage'"/>
    </div>
  </d>
</template>
<script>


import NavBar from "@/components/NavBar";
import StakeInstance from "@/components/StakeEngine/StakeInstance";
import StakeIntroduction from "@/components/StakeEngine/StakeIntroduction";
import StakeGetStarted from "@/components/StakeEngine/StakeGetStarted";
import StakeStandard from "@/components/StakeEngine/StakeStandard";
import StakeRewards from "@/components/StakeEngine/StakeRewards";
import StakeVirtualMachine from "@/components/StakeEngine/StakeVirtualMachine";
import StakeDeposits from "@/components/StakeEngine/StakeDeposits";
import UISetUp from "@/components/StakeUI/UISetUp";
import SecurityPage from "@/components/SecurityPage";
import StakeLog from "@/components/StakeEngine/StakeLog";
import StakeVerifyUpload from "@/components/StakeEngine/StakeVerifyUpload";
import {OfficialDiscord, OfficialTwitter} from "@/env";
import GreetingPage from "@/components/GreetingPage";
import {useLocalStorage} from '@vueuse/core';
import UICustom from "@/components/StakeUI/UICustom";
import StakeClusters from "@/components/StakeEngine/StakeClusters";
import ThemeButton from "@/components/ThemeButton";
import {routePaths, pagePaths} from "@/env";

export default {
  name: 'App',
  components: {
    ThemeButton,
    StakeClusters,
    UICustom,
    GreetingPage,
    StakeVerifyUpload,
    StakeLog,
    SecurityPage,
    UISetUp,
    StakeDeposits,
    StakeVirtualMachine,
    StakeRewards,
    StakeStandard,
    StakeGetStarted,
    StakeIntroduction,
    StakeInstance,
    NavBar
  },
  methods: {
    setActivePage(name){
      this.pageName = name;
      window.location = "/?page=" + routePaths.get(name);
      /*
      window.scrollTo({
        top: 0,
        behavior: "instant"
      })
       */
    },
    setTheme(value){
      if(value){
        document.body.classList.add("dark_theme");
      }
      else{
        document.body.classList.remove("dark_theme");
      }
    },
    changeTheme(){
      this.theme ^= true;
      this.setTheme(this.theme)
    },
    openNavBar(value){
      if(value){
        document.getElementById('sidebar').style.width = '250px'; document.getElementById('blackout').style.display = 'block';
      }
      else{
        document.getElementById('sidebar').style.width = '0'; document.getElementById('blackout').style.display = 'none';
      }
    }
  },
  mounted(){
    if(this.theme === 2){
      if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
        this.theme = 1;
      }
      else{
        this.theme = 0;
      }
    }
    this.setTheme(this.theme)
    let uri = window.location.search.substring(1);
    let params = new URLSearchParams(uri);
    this.pageName = pagePaths.has(params.get("page")) ? pagePaths.get(params.get("page")) : "GreetingPage";
  },
  data(){
    return{
      pageName: useLocalStorage("activeIndex", "GreetingPage"),
      theme: useLocalStorage("dark_theme", 2),
      refToDiscord: OfficialDiscord,
      refToTwitter: OfficialTwitter
    }
  }
}
</script>

<style>
@import "./styles_light.css";
@import "./styles_dark.css";
.blackout {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0; left: 0;
  background: rgba(0,0,0,.5);
  z-index: 5;
  display: none;
}
.left_bar{
  z-index: 6;
}
.body.dark_theme .blackout{
  background: rgba(0,0,0,.75);
}
</style>
