<template>
  <button class="row btn" :class="isTitle? 'title_row' : 'title_subrow'" v-on:click="(() => {setPage(next); open(false);})"
          style="width: 220px; transition: 0.5s;">
    <h6 :class="activePage===next? 'light' : 'title'" :style="isTitle? '': 'padding-left: 27px'">{{text}}</h6>
    <span :class="activePage===next? 'light' : 'title'" style="right: 10px; margin-right: auto;">&#10095;</span>
  </button>
</template>

<script>
export default {
  name: "NavRefButton",
  props: ['setPage', 'activePage', 'text', 'next', 'isTitle', 'open']
}
</script>

<style>
.btn{
  background-color: transparent;
  border: none;
  padding: 0;
}
</style>
