<template>
  <button class="row title_row btn" v-on:click="open()" style="width: 220px;">
    <h6 class="title">{{title}}</h6>
    <span class="title arrow" :class="isOpened? 'rotated': ''" style="right: 10px; margin-right: auto;">&#10095;</span>
  </button>
  <div :class="isOpened? 'opened': 'closed'" style="transition: 0.5s; overflow: hidden;" :style="isOpened? 'height: '+(childCount*37).toString()+'px' : ''">
    <slot></slot>
  </div>
</template>

<script>
import {useLocalStorage} from "@vueuse/core";

export default {
  name: "SubBar",
  props: ['title', 'childCount'],
  methods: {
    open(){
      this.isOpened ^= true;
    }
  },
  data() {
    return{
      isOpened: useLocalStorage(this.title+"_KKK", 0)
    }
  },

}
</script>

<style>
.btn{
  background-color: transparent;
  border: none;
  padding: 0;
}
.arrow{
  transition: 0.5s;
  pointer-events: none;
}
.rotated{
  transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
}
.closed{
  height: 0;
}
</style>